/*
    Smash Balloon Common CSS Library
    Used across all Smashballoon WP Plugins
*/
:root {
    --orange-dflt: #ED4944;
    --orange-hvr: #CC3F3A;
    --blue-dflt: #0068A0;
    --blue-hvr: #005B8C;
    --red-dflt: #D72C2C;
    --red-hvr: #AF2121;
    --gray-dflt: #F9F9FA;
    --gray-hvr: #F3F4F5;
    --customizer-blue: #0096CC;
    --notice-brown: #CC7A00;
    --success-green: #59AB46;
}

#wpbody-content {
    padding-bottom: 0px;
}

#wpcontent {
    padding-left: 0px;
}

#sb-app {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
    -moz-osx-font-smoothing: antialiased;
    -webkit-font-smoothing: antialiased;
}

.copyToClipBoard {
    width: 0px;
    height: 0px;
    position: absolute;
    left: -100000px;
}

.sb-fs {
    width: 100%;
    position: relative;
    float: left;
    box-sizing: border-box;
}

.sb-fs-absolute {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

.sb-tr-1 {
    -webkit-transition: all .1s ease-in-out;
    transition: all .1s ease-in-out;
}

.sb-tr-2 {
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

.sb-tr-3 {
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}

.sb-tr-4 {
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
}

.sb-tr-5 {
    -webkit-transition: all .5s ease-in-out;
    transition: all .5s ease-in-out;
}

.sb-tr-6 {
    -webkit-transition: all .6s ease-in-out;
    transition: all .6s ease-in-out;
}

.sb-tr-7 {
    -webkit-transition: all .7s ease-in-out;
    transition: all .7s ease-in-out;
}

.sb-tr-8 {
    -webkit-transition: all .8s ease-in-out;
    transition: all .8s ease-in-out;
}

.sb-tr-9 {
    -webkit-transition: all .9s ease-in-out;
    transition: all .9s ease-in-out;
}

.sb-tr-10 {
    -webkit-transition: all 1s ease-in-out;
    transition: all 1s ease-in-out;
}

.sb-relative {
    position: relative;
}

.sb-flex {
    display: flex;
}
/*
    Smash Plugins Menu Icons
*/
#toplevel_page_cff-top .toplevel_page_cff-top .wp-menu-image:before {
    content: "\f305";
}
#toplevel_page_sb-instagram-feed .toplevel_page_sb-instagram-feed .wp-menu-image:before {
    content: "\f306";
}
#toplevel_page_custom-twitter-feeds .dashicons-admin-generic:before {
    content: "\f301";
}

.sb-link{
    color: #0068A0;
    cursor: pointer;
    text-decoration: none;
    align-items: center;
}
.sb-link svg{
    margin: 0 8px;
    fill: currentColor;
}

.sb-svg-i svg,
.sb-svg-p svg path {
    fill: currentColor;
}

/* Text & Fonts */
.sb-small-p,
.sb-standard-p {
    font-style: normal;
    font-weight: normal;
    line-height: 160%;
    font-weight: 400;
    margin: 0;
}

.sb-standard-p {
    font-size: 16px;
    color: #141B38;
}

.sb-small-p {
    font-size: 14px;
    color: #8C8F9A;
}

.sb-bold {
    font-weight: 600;
}

.sb-text {
    font-size: 16px;
}

.sb-text-small {
    font-size: 14px;
}

.sb-text-tiny {
    font-size: 12px;
}

.sb-dark-text {
    color: #141B38;
}

.sb-dark2-text {
    color: #434960;
}

.sb-light-text {
    color: #8C8F9A;
}
.sb-light-text2 {
    color: #696D80;
}

.sb-pro-label{
    display: inline-block;
    width: 20px;
    height: 16px;
    padding: 0px 4px;
    gap: 10px;
    background: #FE544F;
    color: #fff;
    border-radius: 2px;
    font-weight: 700;
    font-size: 10px;
    margin: 0 10px;
    line-height: 1.7em;
}

/* H? header */
.sb-h1,
.sb-h2,
.sb-h3,
.sb-h4 {
    font-weight: 600;
    line-height: 125%;
    padding: 0;
    margin: 0;
}

.sb-h1 {
    font-size: 36px;
}

.sb-h2 {
    font-size: 32px;
}

.sb-h3 {
    font-size: 24px;
}

.sb-h4 {
    font-size: 18px;
}

#sb-app h3 {
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 120%;
    margin: 0 0 4px 0;
    letter-spacing: 0;
}

/* Close Button */
.sb-cls-ctn {
    height: 20px;
    width: 20px;
    position: absolute;
    cursor: pointer;
    right: 0;
    top: 0;
    z-index: 2;
}

.sb-cls-ctn:before,
.sb-cls-ctn:after {
    content: '';
    position: absolute;
    height: 2px;
    width: 20px;
    background: #696D80;
    left: 1px;
    top: 6px;
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
}

.sb-cls-ctn:after {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}

.sb-modal-cls {
    right: 16px;
    top: 16px;
}

/*Loading Bar*/
.sb-loadingbar-ctn {
    position: absolute;
    height: 5px;
    width: 100%;
    left: 0px;
    bottom: 0px;
    background: rgba(227, 79, 14, 0.25);
    z-index: 999999999999999;
}

.sb-loadingbar-ctn:before,
.sb-loadingbar-ctn:after {
    content: '';
    position: absolute;
    height: 5px;
    background: #FE544F;
    z-index: 9;
    top: 0;
}

.sb-loadingbar-ctn:before {
    -webkit-animation: sb-loading-animation 4s infinite;
    animation: sb-loading-animation 4s infinite;
}

.sb-loadingbar-ctn:after {
    -webkit-animation: sb-loading-animation 4s 2s infinite;
    animation: sb-loading-animation 4s 2s infinite;
}

@-webkit-keyframes sb-loading-animation {
    from {
        left: -5%;
        width: 0%;
    }

    to {
        left: 130%;
        width: 50%;
    }
}

@keyframes sb-loading-animation {
    from {
        left: -5%;
        width: 0%;
    }

    to {
        left: 130%;
        width: 50%;
    }
}

/*
    SB Notification
*/
.sb-notice {
    width: 100%;
    float: left;
    position: relative;
    padding: 12px 12px 12px;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    border-radius: 4px;
    box-sizing: border-box;
}

.sb-notice-icon {
    margin-right: 10px;
    margin-top: 3px;
}

.sb-notice-icon svg {
    width: 15px;
    height: 15px;
    float: left;
}

.sb-notice-default {
    color: #663D00;
    background: #FFF7E5;
    border: 1px solid #FFDF99;
}
.sb-notice svg,
.sb-notice svg path{
    fill: currentColor;
}
.sb-notice-error {
    color: var(--red-dflt);
    background: #ffe5e5;
    border: 1px solid #ed8f8f;
}

.sb-template-chooser-btn {
    margin-bottom: 25px;
}
.sb-notice-secondry {
    color: #141B38;
    background: #F9F9FA;
    border: 1px solid #E6E6EB;
}
/*
    SB Notification
*/
.sb-notification-ctn {
    position: fixed;
    bottom: -100px;
    left: 200px;
    z-index: 9999999999;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    border-left: 3px solid #fff;
    line-height: 1em;
    padding: 10px 20px;
    padding-left: 0px;
    border-radius: 4px;
    box-shadow: 0px 26.7377px 77.2886px rgba(0, 0, 0, 0.107828), 0px 14.2952px 41.3222px rgba(0, 0, 0, 0.0894161), 0px 8.01379px 23.1649px rgba(0, 0, 0, 0.075), 0px 4.25607px 12.3027px rgba(0, 0, 0, 0.0605839), 0px 1.77104px 5.11942px rgba(0, 0, 0, 0.0421718);

}
body.folded .sb-notification-ctn {
    left: 75px;
}

.sb-notification-ctn[data-active="hidden"] {
    -webkit-animation: sb-notification-hide .5s forwards linear;
    animation: sb-notification-hide .5s forwards linear;
}

.sb-notification-ctn[data-active="shown"] {
    -webkit-animation: sb-notification-show .5s forwards linear;
    animation: sb-notification-show .5s forwards linear;
}

@-webkit-keyframes sb-notification-show {
    0% {
        bottom: -100px;
    }

    50% {
        bottom: 70px;
    }

    70% {
        bottom: 60px;
    }

    85% {
        bottom: 65px;
    }

    100% {
        bottom: 50px;
    }
}

@keyframes sb-notification-show {
    0% {
        bottom: -100px;
    }

    50% {
        bottom: 70px;
    }

    70% {
        bottom: 60px;
    }

    85% {
        /*Loading Bar*/
    }

    100% {
        bottom: 50px;
    }
}

@-webkit-keyframes sb-notification-hide {
    0% {
        bottom: 50px;
    }

    55% {
        bottom: 65px;
    }

    70% {
        bottom: 60px;
    }

    85% {
        bottom: 70px;
    }

    100% {
        bottom: -100px;
    }
}

@keyframes sb-notification-hide {
    0% {
        bottom: 50px;
    }

    55% {
        bottom: 65px;
    }

    70% {
        bottom: 60px;
    }

    85% {
        bottom: 70px;
    }

    100% {
        bottom: -100px;
    }
}

.sb-notification-ctn[data-type="success"] {
    border-color: #59AB46;
}

.sb-notification-ctn[data-type="error"] {
    border-color: #D72C2C;
}

.sb-notification-ctn[data-type="message"] {
    border-color: #141B38;
}

.sb-notification-icon {
    width: 25px;
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 11px;
    box-sizing: border-box;
    padding: 5px;
    border-radius: 50%;
}

.sb-notification-icon svg {
    width: 100%;
    height: 100%;
    float: left;
    fill: #fff;
}

.sb-notification-icon svg,
.sb-notification-icon svg path {
    fill: #fff;
}

.sb-notification-ctn[data-type="success"] .sb-notification-icon {
    background: #59AB46;
}

.sb-notification-ctn[data-type="error"] .sb-notification-icon {
    background: #D72C2C;
}

.sb-notification-ctn[data-type="message"] .sb-notification-icon {
    background: #141B38;
}

.sb-notification-ctn span {
    font-size: 14px;
    color: #141B38;
    font-weight: 600;
}

/*
    Top Notice Bar
*/
.sb-noticebar-ctn {
    text-align: center;
    padding: 7px;
    margin-bottom: 0;
    opacity: 1;
    transition: all .3s ease-in-out;
    max-height: 100px;
    overflow: hidden;
    color: #fff;
    width: auto;
    position: relative;
    top: 0;
    z-index: 1;
}
.sb-customizer-header .sb-noticebar-ctn:not(:first-of-type) {
    position: absolute;
    width: 100%;
    z-index: 0;
}

.sb-noticebar-ctn .sb-cls-ctn:before,
.sb-noticebar-ctn .sb-cls-ctn:after{
    background-color: #fff;
}
.sb-noticebar-ctn  .sb-cls-ctn{
    top: 10px;
}

.sb-noticebar-ctn[data-type="default"] {
    background-color: var(--blue-dflt);
    color: #fff;
}

.sb-noticebar-ctn[data-type="important"] {
    background-color: var(--notice-brown);
    color: #fff;
}
.sb-noticebar-ctn[data-type="error"] {
    background-color: var(--red-dflt);
    color: #fff;
}



.sb-noticebar-heading {
    font-size: 14px;
    line-height: 160%;
    font-weight: 300;
    font-weight: 400;
}

.sb-noticebar-heading strong {
    margin-left: 5px;
    cursor: pointer;
    text-decoration: underline;
}


/*
    Full Screen Loadder
*/
.sb-fs-loader {
    position: fixed;
    width: calc(100% - 160px);
    height: calc(100% - 32px);
    background: #f0f0f1;
    z-index: 999999;
    overflow: hidden;
    display: none;
    opacity: 0;
    visibility: hidden;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    top: 32px;
    left: 160px;
}

/*Loading Bar*/
body.folded .sb-fs-loader {
    width: calc(100% - 36px);
    left: 36px;
}

.sb-fs-loader {
    display: flex;
    opacity: 1;
    visibility: visible;
}

.sb-fs-loader-logo {
    width: 190px;
    height: 190px;
    position: relative;
    margin-bottom: 40px;
    margin-top: -55px;
}

.sb-fs-loader-border {
    width: inherit;
    height: inherit;
    box-sizing: border-box;
    border: 6px solid green;
    left: 0;
    top: 0;
    position: absolute;
    border-radius: 50%;
}

.sb-fs-loader-img {
    width: inherit;
    height: inherit;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 2;
}

/*Loading Bar*/

.sb-fs-loader-txt {
    color: #434960;
    font-size: 24px;
    font-weight: 600;
}

.sb-fs-loader-spinner,
.sb-fs-loader-spinner:before,
.sb-fs-loader-spinner:after {
    border-radius: 50%;
}

.sb-fs-loader-spinner {
    color: #FE544F;
    position: relative;
    width: 190px;
    height: 190px;
    box-shadow: inset 0 0 0 6px;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
}

.sb-fs-loader-spinner:before,
.sb-fs-loader-spinner:after {
    position: absolute;
    content: '';
}

.sb-fs-loader-spinner:before {
    width: 100px;
    height: 200px;
    background: #f0f0f1;
    border-radius: 190px 0 0 190px;
    top: -1px;
    left: -8px;
    -webkit-transform-origin: 5.1em 5.1em;
    transform-origin: 100px 100px;
    -webkit-animation: sb-loader-spinner 2s infinite ease 1.5s;
    animation: sb-loader-spinner 2s infinite ease 1.5s;
}

.sb-fs-loader-spinner:after {
    width: 97px;
    height: 192px;
    background: #f0f0f1;
    border-radius: 0 190px 190px 0;
    top: -1px;
    left: 94px;
    -webkit-transform-origin: 1px 95px;
    transform-origin: 1px 95px;
    -webkit-animation: sb-loader-spinner 2s infinite ease;
    animation: sb-loader-spinner 2s infinite ease;
}

@-webkit-keyframes sb-loader-spinner {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@keyframes sb-loader-spinner {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

/*
    License Flow
*/
.sb-dash-license-ctn{
    max-width: 1200px;
    min-height: 100px;
    overflow: auto;
    background: #fff;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05), 0px 4px 5px rgba(0, 0, 0, 0.05);
    border-radius: 8px;
    margin: auto;
    margin-bottom: 100px;
}
.sb-dash-license-section{
    padding: 65px 25%;
}
.sb-dash-license-section.sb-dash-license-centered-cards {
    padding: 65px 15%;
}
.sb-dash-license-section:nth-of-type(even) {
    background: #F9F9FA;
}
.sb-dash-license-centered{
    display: flex;
    width: 100%;
    align-items: center;
    flex-direction: column;
}
.sb-dash-license-centered img{
    width: 184px;
}

.sb-dash-license-logo{
    margin-bottom: 30px;
}
.sb-dash-license-input-ctn{
    display: flex;
    column-gap: 10px;
    margin-top: 30px;
}
.sb-dash-license-buy-ctn{
    height: 38px;
    margin-top: 10px;
    background: #F9F9FA;
    border: 1px solid #E6E6EB;
    border-radius: 4px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    column-gap: 20px;
}
.sb-dash-license-buy-ctn > a{
    margin-right: 30px;
}
.sb-dash-license-info-cards{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 10px;
    margin-top: 40px;
}
.sb-dash-license-card {
    background: #fff;
    box-sizing: border-box;
    padding: 22px;
    display: flex;
    flex-direction: column;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05), 0px 4px 5px rgba(0, 0, 0, 0.05);
    border-radius: 8px;
}
.sb-dash-license-card svg{
    width: 30px;
    height: auto;
    margin-bottom: 20px;
}
.sb-dash-license-support-heading{
    margin-top: 25px;
    margin-bottom: 30px;
}
.sb-dash-license-card > p{
    margin: .7em 0;
}


/*
	Modals
*/
.sb-modal-ctn {
    position: fixed;
    height: 100vh;
    width: 100%;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background: rgba(0, 0, 0, .4);
    z-index: 9989;
    display: flex;
    justify-content: center;
    align-items: center;
}

.sb-modal-insider {
    left: 80px;
    max-width: calc(100% - 200px);
    max-height: 90vh;
    background: #fff;
    color: #141B38;
    position: relative;
    box-shadow: 0px 26.7377px 77.2886px rgb(0 0 0 / 11%),
        0px 14.2952px 41.3222px rgb(0 0 0 / 9%),
        0px 8.01379px 23.1649px rgb(0 0 0 / 8%),
        0px 4.25607px 12.3027px rgb(0 0 0 / 6%),
        0px 1.77104px 5.11942px rgb(0 0 0 / 4%);
    border-radius: 8px;
    overflow-y: auto;
}

.sb-modal-ctn[data-size="tiny"] .sb-modal-insider {
    width: 460px;
}

.sb-modal-ctn[data-size="small"] .sb-modal-insider {
    width: 580px;
}

.sb-modal-ctn[data-size="medium"] .sb-modal-insider {
    width: 876px;
}

.sb-modal-ctn[data-size="large"] .sb-modal-insider {
    width: 1170px;
}

.sb-modal-ctn[data-size="full"] .sb-modal-insider {
    width: calc(100% - 50px);
}
.sb-modal-insider h4{
    margin-bottom: 4px;
}

/*
Confirm Dialog Box Modal
*/
.sb-confirm-modal {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
    padding: 38px 12%;
}

.sb-confirm-modal-heading {
    font-size: 20px;
    display: block;
    margin-bottom: 15px;
    line-height: 160%;
}

.sb-confirm-modal-description {
    line-height: 160%;
}

.sb-confirm-modal-btns {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 25px;
}

.sb-confirm-modal-btns button {
    width: 100%;
    margin: 4px 4px;
    cursor: pointer;
}

/* Feed templates Modal */
.sb-feedtemplates-modal {
    display: flex;
    justify-content: center;
}

.sb-feedtemplates-modal-content {
    width: 865px;
    padding: 35px 0px;
