/* Add Source Modal  */
.sb-addsource-modal-heading {
    padding: 16px 32px;
}

.sb-addsource-modal-navtop {
    display: flex;
    justify-content: center;
    align-items: center;
}

.sb-addsource-modal-navitem {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
    height: 50px;
    border-bottom: 1px solid #E6E6EB;
    cursor: pointer;
}

.sb-addsource-modal-navitem-num {
    width: 20px;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 11px;
    font-weight: 600;
    color: #2C324C;
    background: #E6E6EB;
    border-radius: 40px;
    margin-right: 10px;
    line-height: 1em;
}
.sb-addsource-modal{
    height: auto;
