/*
    SB Tooltip
*/
.sb-tooltip {
    position: absolute;
    border-radius: 2px;
    font-size: 12px;
    font-weight: 400;
    padding: 4px 10px;
    line-height: 160%;
    opacity: 0;
    visibility: hidden;
    box-shadow: 0 5px 9px rgb(0 0 0 / 20%), 0 -4px 9px rgb(0 0 0 / 10%);
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    z-index: 99;
}

.sb-tooltip[data-type="default"] {
    background: #2C324C;
    color: #fff;
}

.sb-tooltip[data-type="white"] {
    background: #fff;
    color: #2C324C;
}

.sb-tooltip[data-width="full"] {
    width: 100%;
    padding: 0px 15px;
}

.sb-tooltip[data-textalign="center"] {
    text-align: center;
}
.sb-tooltip[data-textalign="left"] {
    text-align: left;
}
.sb-tooltip[data-textalign="right"] {
    text-align: right;
}

.sb-tooltip[data-position="top-center"] {
    top: calc(-100% - 30px);
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
}

.sb-tooltip[data-position="top-center"]:after {
    content: '';
    width: 10px;
    height: 10px;
    position: absolute;
    left: 50%;
    bottom: -4px;
    background: inherit;
    -webkit-transform: translateX(-50%) rotate(45deg);
    transform: translateX(-50%) rotate(45deg);
}

*:hover>.sb-tooltip {
    opacity: 1;
    visibility: visible;
}

*:hover>.sb-tooltip[data-position="top-center"] {
    top: -120%;
}

/*
    SB Button
    The Global Button Component
*/
.sb-btn {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    outline: none;
    padding: 0px;
    border: 0px;
    font-weight: 600;
    box-shadow: 0px 2px 5px rgba(60, 66, 87, 0.12),
            0px 1px 1px rgba(0, 0, 0, 0.08),
            inset 0px -1px 1px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    -webkit-transition: all .15s ease-in-out;
    transition: all .15s ease-in-out;
}
.sb-btn.sb-btn-secondary {
    box-shadow: 0px 2px 5px rgb(60 66 87 / 5%),
            0px 1px 1px rgb(0 0 0 / 5%);
}

.sb-btn[data-boxshadow="false"],
.sb-btn[data-boxshadow="false"]:focus,
.sb-btn[data-boxshadow="false"]:active {
    box-shadow: unset;
}

.sb-btn:active {
    -webkit-transform: scale(.98);
    transform: scale(.98);
}

.sb-btn:focus {
    box-shadow: 0px 2px 5px rgba(60, 66, 87, 0.05),
        0px 1px 1px rgba(0, 0, 0, 0.05),
        inset 0px -1px 1px rgba(0, 0, 0, 0.12);
}

.sb-btn[data-full-width="true"] {
    width: 100%;
}

.sb-btn-disabled {
    opacity: .6 !important;
}

.sb-btn svg {
    float: left;
    fill: currentColor;
    height: auto;
}

.sb-btn svg path {
    fill: currentColor;
}

.sb-btn[data-icon-position="left"]:not([data-onlyicon="true"]) {
    flex-direction: row;
}

.sb-btn[data-icon-position="right"]:not([data-onlyicon="true"]) {
    flex-direction: row-reverse;
}

.sb-btn[data-icon-position="left"]:not([data-onlyicon="true"]) .sb-btn-icon {
    margin-right: 7px;
}

.sb-btn[data-icon-position="right"]:not([data-onlyicon="true"]) .sb-btn-icon {
    margin-left: 7px;
}

.sb-btn[data-loading="true"] .sb-btn-icon{
    -webkit-animation: sb-button-rotation 1.5s infinite linear;;
    animation: sb-button-rotation 1.5s infinite linear;;
}


@keyframes sb-button-rotation {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(359deg);
    }
}


