    cursor: pointer;
    display: inline-flex
}
.sb-licensekey-test-icon{
    width: 16px;
    height: 16px;
    border-radius: 50px;
    position: relative;
    margin-right: 5px;
}
.sb-licensekey-test-icon[data-icon="success"] {
    background: var(--success-green);
    color: #fff;
}
.sb-licensekey-test-icon[data-icon="success"]:before {
    content: '';
    position: absolute;
    width: 7px;
    height: 3px;
    left: 3px;
    top: 5px;
    border-left: 2px solid currentColor;
    border-bottom: 2px solid currentColor;
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
}
.sb-licensekey-test-icon[data-icon="true"]{
    clip: rect(0, 16px, 6px, 0px);
    -webkit-animation: sb-loader-spinner 1.8s cubic-bezier(0.770, 0.000, 0.175, 1.000) infinite;
    animation: sb-loader-spinner 1.8s cubic-bezier(0.770, 0.000, 0.175, 1.000) infinite;
}
.sb-licensekey-test-icon[data-icon="true"]:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    height: 16px;
    width: 16px;
    clip: rect(0, 16px, 6px, 0px);
    border: 2px solid #222;
    border-radius: 50%;
    -webkit-animation: sb-loader-spinner 1.8s cubic-bezier(0.770, 0.000, 0.175, 1.000) infinite;
    animation: sb-loader-spinner 1.8s cubic-bezier(0.770, 0.000, 0.175, 1.000) infinite;
}

.sb-licensekey-test-icon[data-icon="error"] {
    background: var(--red-dflt);
    color: #fff;
}

.sb-licensekey-test-icon[data-icon="error"]:before,
.sb-licensekey-test-icon[data-icon="error"]:after {
    content: '';
    position: absolute;
    width: 10px;
    height: 2px;
    top: 7px;
    left: 3px;
    background: currentColor;
    -webkit-transform: rotate(-45deg);
        transform: rotate(-45deg);
}
.sb-licensekey-test-icon[data-icon="error"]:after {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}

